<template>
<div class="footer border-1px">
  <router-link  class="router-link" to="/zhiWei">
    <span class="icon i-zhiwei"></span>
    <span>职位</span>
  </router-link> 
  <router-link class="router-link" to="/message">
    <span class="icon i-message"></span>
    <span>消息</span>
  </router-link>
  <router-link class="router-link" to="/resume">
    <span class="icon i-resume"></span>
    <span>简历</span>
  </router-link>
  <router-link class="router-link" to="/mine">
    <span class="icon i-mine"></span>
    <span>我的</span>
  </router-link>
</div>
</template>
<script type="text/ecmascript-6">
 export default {
  data() {
    return {
    }

  },
  components: {
  }
 }
</script>

<style scoped >
.footer{
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  text-align: center;
  width: 100%;
  height:45px;
  font-size: 12px;
  line-height: 12px;
  background-color: #fff; 
}
.footer:before{
  position: absolute;
  content: '';
  display: block;
  width: 100%;
  border-top: 1px solid rgb(219, 219, 219);
}
.router-link{
  flex: 1
}
.icon{
  margin: 5px auto;
  display: block;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-size: 20px 20px;  
}
.i-zhiwei{
  background-image: url(zhiWei.png)
}
.i-message{
  background-image: url(message.png)
}
.i-resume{
  background-image: url(resume.png)
}
.i-mine{
  background-image: url(my.png)
}
@media (-webkit-min-device-pixel-ratio:2),(min-device-pixel-ratio:2){
  .border-1px::before{
    transform:scaleY(0.5); 
  }
}
</style>



