<template>
	<div class="zhiWei">
		<div class="header">
			<div ref="banner" class="banner">
				<div  class="banner-item" :class="{hideItem: hideItem[0]}" :style="{left:ItemWidth[0]+'px'}"><img :width="width" src="../../static/img/1.jpg" alt=""></div>
				<div class="banner-item" :class="{hideItem: hideItem[1]}" :style="{left:ItemWidth[1]+'px'}"><img :width="width" src="../../static/img/2.jpg" alt=""></div>
				<div class="banner-item" :class="{hideItem: hideItem[2]}" :style="{left:ItemWidth[2]+'px'}"><img :width="width" src="../../static/img/3.jpg" alt=""></div>
			</div>
			<input type="text" class="input-search" placeholder="搜索职位/公司">
			<div class="saoYiao"></div>
		</div>
		<div class="school-zhaoPin">
			<div class="content">
				<div class="school-img"><img width="55px" height="55px" src="../../static/img/大学生.png" alt=""></div>
				<div class="school-message">
					<div class="school-title">校园招聘</div>
					<div class="school-remarks">大学生求职专属</div>
				</div>
			</div>
			<!------>
			<div class="content">
				<div class="school-img"><img width="55px" height="55px" src="../../static/img/大学生2.png" alt=""></div>
				<div class="school-message">
					<div class="school-title">面试直通车</div>
					<div class="school-remarks">直接与企业约面试</div>
				</div>
			</div>
		</div>
		<div class="main">
			<ul class="company-message">
				<li v-if="data" class="company-content" v-for="(item,index) of data" :key="index">
					<div class="company-title">{{item.companyMessage.companyTitle}}<span class="company-money">{{item.companyMessage.companyMoney}}</span></div>
					<div class="company-claim"><span v-for="itemClaim in item.companyMessage.companyClaim">{{itemClaim}}</span><span class="company-invite">{{item.companyMessage.recruitMessage}}</span></div>
					<div class="company-welfare"><span v-for="itemWelfare in item.companyMessage.companyClaim">{{itemWelfare}}</span></div>
					<div class="company-name"><div class="company-logo"></div>{{item.companyName}}<i></i><span></span></div>
				</li>
			</ul>
		</div>
	</div>
</template>
<script type="text/ecmascript-6">
	import Vue from 'vue'
	export default {
		name: 'zhiWei',
		data() {
			return {
				ItemWidth:[],
				width: 0,
				bannerItem: '',
				hideItem: [false,false,false],
				data: ""
			}
		},
		computed: {
		},
		methods: {
			init() {
				var banner = this.$refs.banner;
				this.bannerItem =banner.getElementsByClassName('banner-item');
				for(var i = 0; i<this.bannerItem.length; i++){
					this.ItemWidth.push(this.width);
					 this.width += window.innerWidth;
				}
				this.width = window.innerWidth;
			},
			moveleft1() {

				var _this = this;
				for (var i=0; i<_this.ItemWidth.length; i++){
					if(this.ItemWidth[i]<0){
						_this.hideItem[i] = true;
						this.ItemWidth[i] = _this.width * (_this.ItemWidth.length-2);
					}else{
							_this.hideItem[i] = false;
						this.$set(this.ItemWidth,i,this.ItemWidth[i]-_this.width);
					}
				}
			 }
			},
		beforeCreate() {
			console.log(1);
			this.$http.get("../../static/data.json").then(function(res){
				this.data =  (JSON.parse(res.bodyText)).zhiWei.companies;
				console.log(this.data[1]);
			})
		},
		mounted() {
			var _this = this;
			this.init();
			setInterval(function () {
				_this.moveleft1()
			}, 5000)
		}
	}
</script>

<style scoped>
.zhiWei{
	height: 100%;
}
.header{
	overflow: hidden;
}
.banner{
	width: 100%;
	height: 185px;
	position: relative;
}
.banner img{
	display: block;
	height: 185px;
}
.banner-item{
	z-index: 10;
	transition: all 3s;
	position: absolute;
	top: 0;
	width: 100%;
	height: 185px;
}
.hideItem{
	z-index: 1;
}
.input-search{
	width: 80%;
	height: 34px;
	left: 13px;
	text-align: center;
	top: 32px;
	background: #ededed;
	border: none;
	position: absolute;
	z-index: 20;
	border-radius: 20px;
	font-size: 14px;
}
.saoYiao{
	position: absolute;
	top: 36px;
	right: 13px;
	z-index: 20;
	width: 20px;
	height: 20px;
	background: url(../../static/img/saoYisao.png) no-repeat;
	background-size: 20px 20px;
}
.saoYiao:after{
	content: '扫一扫';
	top:110%;
	left: -20%;
	position: absolute;
	width: 160%;
	display: block;
	color:white;
	font-size: 2px;
	font-weight: 200;
}
.school-zhaoPin{
	display: flex;
	flex-direction: row;
	padding: 10px 0;
}
.school-zhaoPin>.content{
	flex:1;
	padding-left:15px;
}
.school-zhaoPin .content>div{
	float: left;
}
	.school-title{
		font-size: 16px;
		color: #2e2e2e;
		font-weight: 400;
	}
	.school-message{
		padding-top: 10px;
		padding-left: 10px;;
	}
	.school-remarks{
		font-size: 12px;
		color: #7f7f7f;
	}
.main{
	background-color: #f8f8fa;
	height: 100%;
}
	.company-message{
		width: 100%;
		padding-top: 10px;
	}
	.company-message .company-content{
		padding: 12px 15px;
		margin-bottom:10px;
		background-color: #fff;
	}
	.company-title{
		line-height: 16px;
		font-size: 16px;
		font-weight: 700;
	}
	.company-title .company-money{

		color: red;
		float: right;
	}
	.company-claim, .company-welfare{
		color: #b7b7b7;
		font-size: 12px;
	}
	.company-claim {
		margin-top: 10px;
	}
	.company-claim>span{
		margin-right: 10px;;
	}
	.company-claim .company-invite{
		float: right;
	}
	.company-welfare{
		margin-top: 14px;
		margin-bottom: 17px;
	}
	.company-welfare span{
		background-color: #f2f8fb;
		margin-right: 12px;
		display: inline-block;
		padding: 5px;
	}
</style>



